import { useEffect, useState } from 'react';
import './App.css';
import { Button, EditableText, InputGroup, Toaster } from '@blueprintjs/core';

const AppToaster = Toaster.create({ position: 'top' });

function App() {
  const [userData, setUserData] = useState([]);
  const [useName, setUseName] = useState('');
  const [useMail, setUseMail] = useState('');
  const [useAddress, setUseAddress] = useState('');
  const [usePhone, setUsePhone] = useState('');
  const [useWebsite, setUseWebsite] = useState('');
  const [searchQuery, setSearchQuery] = useState('');
  const [searchResults, setSearchResults] = useState([]);

  useEffect(() => {
    fetch('https://jsonplaceholder.typicode.com/users')
      .then((response) => response.json())
      .then((json) => setUserData(json));
  }, []);

  useEffect(() => {
    // Filter user data based on the search query
    const filteredResults = userData.filter(
      (user) =>
        user.name.toLowerCase().includes(searchQuery.toLowerCase()) ||
        user.email.toLowerCase().includes(searchQuery.toLowerCase()) ||
        user.phone.toLowerCase().includes(searchQuery.toLowerCase())
    );
    setSearchResults(filteredResults);
  }, [searchQuery, userData]);

  function addUser() {
    const name = useName.trim();
    const email = useMail.trim();
    const address = useAddress.trim();
    const phone = usePhone.trim();
    const website = useWebsite.trim();

    if (name && email && address && phone && website) {
      fetch('https://jsonplaceholder.typicode.com/users', {
        method: 'POST',
        body: JSON.stringify({
          name,
          email,
          address,
          phone,
          website,
        }),
        headers: {
          'Content-type': 'application/json; charset=UTF-8',
        },
      })
        .then((response) => response.json())
        .then((json) => {
          setUserData([...userData, json]);
          AppToaster.show({
            message: 'User added Successfully',
            intent: 'success',
            timeout: 3000,
          });
        });

      setUseName('');
      setUseMail('');
      setUseAddress('');
      setUsePhone('');
      setUseWebsite('');
    }
  }

  function onChangehandler(id, key, value) {
    setUserData((userData) => {
      return userData.map((user) =>
        user.id === id ? { ...user, [key]: value } : user
      );
    });
  }

  function updateUser(id) {
    const user = userData.find((user) => user.id === id);
    fetch(`https://jsonplaceholder.typicode.com/users/${id}`, {
      method: 'PUT',
      body: JSON.stringify(user),
      headers: {
        'Content-type': 'application/json; charset=UTF-8',
      },
    })
      .then((response) => response.json())
      .then((json) => {
        AppToaster.show({
          message: 'User updated Successfully',
          intent: 'success',
          timeout: 3000,
        });
      });
  }

  function deleteUser(id) {
    fetch(`https://jsonplaceholder.typicode.com/users/${id}`, {
      method: 'DELETE',
    })
      .then((response) => response.json())
      .then((json) => {
        setUserData((userData) => {
          return userData.filter((user) => user.id !== id);
        });
        AppToaster.show({
          message: 'User deleted Successfully',
          intent: 'success',
          timeout: 3000,
        });
      });
  }

  return (
    <div className="App">
      <InputGroup
        value={searchQuery}
        onChange={(e) => setSearchQuery(e.target.value)}
        placeholder="Search by Name, Email, or Phone"
      />

      <table>
        <thead>
          <tr>
            <th>ID</th>
            <th>NAME</th>
            <th>E-MAIL</th>
            <th>ADDRESS</th>
            <th>PHONE</th>
            <th>WEBSITE</th>
            <th>CITY</th>
            <th>ACTION</th>
          </tr>
        </thead>
        <tbody>
          {searchResults.map((user) => (
            <tr key={user.id}>
              <td>{user.id}</td>
              <td>
                <EditableText
                  onChange={(value) => onChangehandler(user.id, 'name', value)}
                  value={user.name}
                />
              </td>
              <td>
                <EditableText
                  onChange={(value) => onChangehandler(user.id, 'email', value)}
                  value={user.email}
                />
              </td>
              <td>
                <EditableText
                  onChange={(value) =>
                    onChangehandler(user.id, 'address', value)
                  }
                  value={`${user.address.street}, ${user.address.suite}, ${user.address.city}`}
                />
              </td>
              <td>
                <EditableText
                  onChange={(value) => onChangehandler(user.id, 'phone', value)}
                  value={user.phone}
                ></EditableText>
              </td>
              <td>
                <EditableText
                  onChange={(value) =>
                    onChangehandler(user.id, 'website', value)
                  }
                  value={user.website}
                />
              </td>
              <td>
                <Button intent="primary" onClick={() => updateUser(user.id)}>
                  EDIT
                </Button>
                &nbsp;
                <Button intent="danger" onClick={() => deleteUser(user.id)}>
                  DELETE
                </Button>
              </td>
            </tr>
          ))}
        </tbody>
        <tfoot>
          <tr>
            <td></td>
            <td>
              <InputGroup
                value={useName}
                onChange={(e) => setUseName(e.target.value)}
                placeholder="Enter Name"
              />
            </td>
            <td>
              <InputGroup
                value={useMail}
                onChange={(e) => setUseMail(e.target.value)}
                placeholder="Enter Email"
              />
            </td>
            <td>
              <InputGroup
                value={useAddress}
                onChange={(e) => setUseAddress(e.target.value)}
                placeholder="Enter Address"
              />
            </td>
            <td>
              <InputGroup
                value={usePhone}
                onChange={(e) => setUsePhone(e.target.value)}
                placeholder="Enter Phone"
              />
            </td>
            <td>
              <InputGroup
                value={useWebsite}
                onChange={(e) => setUseWebsite(e.target.value)}
                placeholder="Enter Website"
              />
            </td>
            <td>
              <Button intent="success" onClick={addUser}>
                INSERT
              </Button>
            </td>
          </tr>
        </tfoot>
      </table>
    </div>
  );
}

export default App;
